* {
  box-sizing: border-box; }

.grid-wrapper {
  display: grid;
  grid-template-columns: 195px auto;
  grid-template-rows: 290px auto; }

nav {
  grid-row: 1;
  grid-column: 1; }

#messages {
  grid-row: 1;
  grid-column: 2; }

.navbar-nav {
  margin-left: 20px; }
  .navbar-nav .nav-link {
    color: #1d1d1d !important; }

.btn {
  background-color: #fd9453;
  border-color: #fd9453;
  color: #fff;
  box-shadow: 1px 1px 4px 0 #1d1d1d; }
  .btn:active, .btn:hover, .btn:focus {
    background-color: #fb9d62;
    border-color: #fb9d62;
    color: #fff;
    box-shadow: 1px 1px 4px 0 #1d1d1d; }

.navbar-brand {
  margin: 10px 0 0 20px;
  color: #1d1d1d;
  text-transform: uppercase;
  font-family: Arial, Helvetica, sans-serif; }
  .navbar-brand:hover {
    color: #1d1d1d; }
  .navbar-brand .badge {
    border: solid 1px #fd9453;
    color: #fd9453;
    vertical-align: top;
    font-size: 8px; }

.nav-pills .nav-link {
  color: #55c2bb; }
  .nav-pills .nav-link:hover {
    color: #3faca4; }

.nav-pills .nav-link.active {
  background-color: #55c2bb !important; }
  .nav-pills .nav-link.active:hover {
    color: #fff; }

.landing-content {
  margin: 33vh auto 0;
  color: #1d1d1d;
  text-transform: uppercase;
  font-family: Arial, Helvetica, sans-serif;
  text-align: center;
  position: relative; }
  .landing-content .brand {
    font-size: 60px;
    letter-spacing: 4px; }
  .landing-content .badge {
    position: absolute;
    border: solid 1px #fd9453;
    letter-spacing: 1px;
    color: #fd9453;
    vertical-align: top;
    font-size: 15px; }
  .landing-content .motivation {
    font-size: 16px;
    letter-spacing: 1px;
    margin-bottom: 16px; }
  .landing-content .empty-button {
    border: solid 3px #fd9453;
    border-radius: 4px;
    letter-spacing: 1px;
    font-size: 24px;
    color: #fd9453;
    padding: 3px 55px; }
    .landing-content .empty-button:hover {
      text-decoration: none;
      background-color: #fd9453;
      border-color: #fd9453;
      color: #fff; }
